import {Component, OnInit} from '@angular/core';
import {SzavazoGombComponent} from "../szavazogomb/szavazogomb.component";
import {hunkCurrencyPipe} from "../../globals/pipes/hunkCurrency.pipe";
import {APIService} from "../../globals/services/API.service";

@Component({
    selector: 'full-right-panel',
    moduleId:  module.id,
    templateUrl: 'fullRightPanel.html',

    styleUrls: ["fullRightPanel.css"],
    directives: [SzavazoGombComponent],
    pipes: [hunkCurrencyPipe],
    inputs: ['ingatlanObject', 'deitalPanelBlock'],
    providers:[APIService]

})
export class FullRightPanelComponent implements OnInit{
    ingatlanObject:any;
    deitalPanelBlock:String;
    toplista:any[]=[];
    fuggobenVan:number=0;
    constructor( private _APIService:APIService) {

    }
    ngOnInit(){
        this.loasToplista();
        this.getFuggoIngatlanok();
    }
    loasToplista(){
        const apilink='api/ingatlan/licit/toplista/'+this.ingatlanObject.id;
        console.log(apilink);
        this._APIService.getResponseGET (apilink,'').subscribe(
                data=> {
                this.toplista=data;
            },
                error=> {
                console.log(error);
            })
    }
    getFuggoIngatlanok(){
        const apilink='api/ingatlan/licit/fuggobenleve/'+this.ingatlanObject.id;
        console.log(apilink);
        this._APIService.getResponseGET (apilink,'').subscribe(
                data=> {
                    this.fuggobenVan=data;
            },
                error=> {
                console.log(error);
            })
    }
}
